import { Component, OnInit, TemplateRef  } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ActivatedRoute, Router } from "@angular/router";
import { GetInsuranceInfoService } from '../get-insurance-info.service';
import { HttpClient,HttpHeaders,HttpParams } from '@angular/common/http';

@Component({
  selector: 'app-purchase-insurance',
  templateUrl: './purchase-insurance.component.html',
  styleUrls: ['./purchase-insurance.component.scss']
})
export class PurchaseInsuranceComponent implements OnInit {


adult:number=1;
child:number=0;
infant:number=0;
 from:string;
 to:string;
 depart:string;
cabin:string = 'economy';
debug:boolean = false;
username:string='';
package:boolean= false;
invalid:any=0;
progressbar:number = 0;
airlines:any;
hideAirlines:any;
fromArray:any;
toArray:any;
departArray:any;
flexible:boolean = false;
domainDetails:any;
currency:string;
preferredAirlines:boolean=false;
tracking_id:string='';
cid:any;

filterBy:number = 0
dataser:any[] = [];





insurancedetails:any;
response:any;


  modalRef: BsModalRef;
  constructor(private modalService: BsModalService,private data:GetInsuranceInfoService,private router:Router,private route:ActivatedRoute,private http: HttpClient) {

    let that = this;

    // this.dataser = that.data.getImages();

    this.route.queryParams.subscribe(params => {

      that.insurancedetails = params.insurancedetails;

       console.log(that.insurancedetails);

    });

       this.http.get('https://api.github.com/users/' + this.insurancedetails)
        .subscribe((response) => {
            this.response = response;
            console.log(this.response)

            localStorage.setItem("user_details", JSON.stringify(this.response));


          }
      );





  }
 
  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template);
  }

  ngOnInit() {
  }



}



