import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { InsuranceRoutingModule } from './insurance-routing.module';
import { PurchaseInsuranceComponent } from './purchase-insurance/purchase-insurance.component';
import { ViewInsuranceComponent } from './view-insurance/view-insurance.component';
import { GetInsuranceInfoService } from './get-insurance-info.service';
import { ModalModule } from 'ngx-bootstrap/modal';
import { HttpClientModule } from '@angular/common/http';


@NgModule({
  imports: [
    CommonModule,
    HttpClientModule,
    InsuranceRoutingModule,
    ModalModule.forRoot()
  ],
  declarations: [PurchaseInsuranceComponent, ViewInsuranceComponent],
  providers:[GetInsuranceInfoService]
})
export class InsuranceModule { }



